import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ENDPOINTS } from '@shared';

@Injectable({
    providedIn: 'root'
})

export class AddressService {

    constructor(
        private http: HttpClient
    ) { }

    getRegions() {
        return this.http.get(`${ENDPOINTS.regions}`);
    }

    getProvinces(regionId) {
        return this.http.get(`${ENDPOINTS.provinces}/region-id/${regionId}`);
    }

    getCityMunicipalities(provinceId) {
        return this.http.get(`${ENDPOINTS.cityMunicipalities}/province-id/${provinceId}`);
    }

    getBarangays(cityMunicipalityId) {
        return this.http.get(`${ENDPOINTS.barangays}/city-municipality-id/${cityMunicipalityId}`);
    }

    getBarangay(barangayId) {
        return this.http.get(`${ENDPOINTS.barangays}/barangay-id/${barangayId}`);
    }

}