import { Component, Inject, OnInit, Optional, ViewChild } from '@angular/core';
import { ENDPOINTS, COMMON_FUNCTION, STATUSES, USER_ROLES, YES_NO_OPTION } from '@shared';
import { Company } from '@models';
import { ApiService, SweetAlertService, DataStorageService } from '@services';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource, MatTable } from '@angular/material/table';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AddressService } from '../../../../core/services/address/address.service';
import { ValidateFieldService } from 'app/core/services/validate-field.service';

@Component({
  selector: 'app-add-company',
  templateUrl: './add-company.component.html',
  styleUrls: ['./add-company.component.scss']
})
export class AddCompanyComponent implements OnInit {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<Company>;
  dataSource: MatTableDataSource<Company>;
  displayedColumns: string[] = ['companyName', 'companyTelephoneNo', 'companyFaxNo', 'companyTin', 'address'];

  countryList = [];
  companyList = [];
  affiliationList = [];
  regionsList = [];
  provinceList = [];
  cityMunicipalityList = [];
  barangayList = [];
  statusList = [...STATUSES];
  yesNoList = [...YES_NO_OPTION];

  company = new Company();
  companyForm: FormGroup;
  currentUser: any;
  todayDate: Date = new Date();
  id: any;
  notForeignAddress = true;
  isFromModal = false;
  isUserRegistration = false;
  showApproveRejectButton = false;

  constructor(
    private apiService: ApiService,
    private addressService: AddressService,
    private formBuilder: FormBuilder,
    private dataStorage: DataStorageService,
    private sweetAlertService: SweetAlertService,
    public validateFieldService: ValidateFieldService,
    private route: ActivatedRoute,
    private router: Router,
    private matDialog: MatDialog,
    @Optional() public dialogRef: MatDialogRef<AddCompanyComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    if (data) {
      if (data.dataForModal) {
        const dataFromModal = data.dataForModal;

        this.isFromModal = dataFromModal.isFromModal;
        this.showApproveRejectButton = dataFromModal.isUpdate;

        if (dataFromModal.isUpdate) {
          this.company = dataFromModal.company
        } else {
          this.company.country = dataFromModal.country
        }
      }
      this.isUserRegistration = data.isUserRegistration;
    }

    const additionalStatus = [
      {
        status: 'APPROVED'
      }, {
        status: 'DISAPPROVED'
      }, {
        status: 'FOR_APPROVAL'
      }
    ];
    this.statusList.push(...additionalStatus);

  }

  ngOnInit(): void {
    this.currentUser = this.dataStorage.getUserAccount();
    this.setFormValidator();
    this.getCountries();
    this.getAffiliationList();
    this.id = Number(this.route.snapshot.paramMap.get('id'));
    if (this.id !== 0) {
      this.viewApplication();
    }
  }

  viewApplication() {
    this.apiService.findById(ENDPOINTS.company, this.id).subscribe((res: any) => {
      this.company = res.responseData.data;
      this.getAffiliationList();
    });
  }

  getCountries() {
    this.apiService.findAll(ENDPOINTS.countries).subscribe((res: any) => {
      this.countryList = res.responseData.data;
    });
  }

  getAffiliationList(): void {
    this.apiService.findAll(ENDPOINTS.activeMembersAffiliation).subscribe((res: any) => {
      this.affiliationList = res.responseData.data;
    });
  }


  save(endpoint) {
    if (!endpoint) {
      endpoint = ENDPOINTS.company;
    }

    if (this.currentUser && (this.currentUser.userRole.name === USER_ROLES.ROLE_ADMIN || this.currentUser.userRole.name === USER_ROLES.ROLE_SUPER_ADMIN)) {
      this.company.status = COMMON_FUNCTION.ACTIVE;
    } else {
      this.company.status = COMMON_FUNCTION.FOR_APPROVAL;
    }

    // if(this.notForeignAddress){
    //   this.company.country = 'PHIL'
    // }

    if (this.isFromModal) {
      this.company.status = COMMON_FUNCTION.FOR_APPROVAL;
    }
    this.company.delinquent = this.setDelinquent(this.company.delinquent);

    this.apiService.save(endpoint, this.company).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.id = res.responseData.data.id;
          this.company = res.responseData.data;
          this.sweetAlertService.customSuccessMessage('Company successfully saved.');
          if (this.isFromModal) {
            this.dialogRef.close(this.company);
          }
          if (this.isUserRegistration) {
            this.dialogRef.close(this.company);
          }
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        // this.resetFormValidator();
        if (!this.isFromModal && !this.isUserRegistration) {
          this.router.navigate(['asc/page/company/update/', this.id]);
        }
      });
  }


  getCompany(id): void {
    this.apiService.findById(ENDPOINTS.company, id).subscribe((res: any) => {
      if (res) {
        this.company = res.responseData.data;
        // this.addressService.getBarangay(res.responseData.data.barangayId).subscribe(
        //   (res: any) => {

        // this.company.region = res.responseData.data.cityMunicipalityDto.provinceDto.regionDto.regionId;
        // this.company.province = res.responseData.data.cityMunicipalityDto.provinceDto.provinceId;
        // this.company.cityMunicipality = res.responseData.data.cityMunicipalityDto.cityMunicipalityId;
        // this.company.barangay = res.responseData.data.barangayId;

        // this.getProvinces();
        // this.getCityMunicipalities();
        // this.getBarangays();
        // });
      }
    });
  }

  update(endpoint) {
    if (!endpoint) {
      endpoint = ENDPOINTS.company;
    }
    this.company.delinquent = this.setDelinquent(this.company.delinquent);

    if (this.company.status === 'APPROVED') {
      this.company.status = 'ACTIVE';
    }
    this.apiService.update(endpoint, this.company).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.cancel();
        if (this.isFromModal) {
          this.dialogRef.close(this.company);
        }
      });
  }

  setFormValidator(): void {
    // if(this.notForeignAddress){
    //   this.companyForm = this.formBuilder.group({
    //     companyName: ['', Validators.required],
    //     companyTelephoneNo: ['', Validators.required],
    //     companyFaxNo: ['', Validators.required],
    //     membersAffiliation: ['', Validators.required],
    //     companyTin: ['', Validators.required],
    //     // region: ['', Validators.required],
    //     // province: ['', Validators.required],
    //     // cityMunicipality: ['', Validators.required],
    //     // barangay: ['', Validators.required],
    //     completeAddress: ['', Validators.required]
    //   });
    // }else{
    //   this.companyForm = this.formBuilder.group({
    //     companyName: ['', Validators.required],
    //     companyTelephoneNo: ['', Validators.required],
    //     companyFaxNo: ['', Validators.required],
    //     membersAffiliation: ['', Validators.required],
    //     companyTin: ['', Validators.required],
    //     country: ['', Validators.required],
    //     foreignAddress: ['', Validators.required]
    //   });
    // }

    this.companyForm = this.formBuilder.group({
      companyName: ['', Validators.required],
      companyMobileNo: ['', Validators.required],
      companyTelephoneNo: ['', Validators.required],
      companyTelephoneNo2: ['', Validators.required],
      // companyFaxNo: ['', Validators.required],
      membersAffiliation: ['', Validators.required],
      contactPersonName: ['', Validators.required],
      contactPersonEmail: ['', [Validators.required, Validators.pattern('[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}')]],
      companyTin: ['', Validators.required],
      country: ['', Validators.required],
      completeAddress: ['', Validators.required],
      voucherFund: [''],
      status: [''],
      delinquent: ['']
    });
  }

  cancel(): void {
    if (!this.isFromModal) {
      if (this.company.id) {
        this.router.navigate(['/asc/page/company/lists']);
      } else {
        this.company = new Company();
        this.companyForm.markAsUntouched();
        this.companyForm.markAsPristine();
      }
    }
  }


  // getRegions() {
  //   this.addressService.getRegions().subscribe(
  //     (res: any) => {
  //       this.regionsList = res;
  //     });
  // }

  // getProvinces() {
  //   this.addressService.getProvinces(this.company.region.regionId).subscribe(
  //     (res: any) => {
  //       this.provinceList = res.responseData.data;
  //     });
  // }

  // getCityMunicipalities() {
  //   this.addressService.getCityMunicipalities(this.company.province.provinceId).subscribe(
  //     (res: any) => {
  //       this.cityMunicipalityList = res.responseData.data;
  //     });
  // }

  // getBarangays() {
  //   this.addressService.getBarangays(this.company.cityMunicipality.cityMunicipalityId).subscribe(
  //     (res: any) => {
  //       this.barangayList = res.responseData.data;
  //     });
  // }

  isForeignAddress(): void {
    this.notForeignAddress = !this.notForeignAddress;
    // this.setFormValidator();
  }

  compareMembersAffiliation(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  compareCountry(value: any, selected: any): boolean {
    if (value && selected) {
      return value.id === selected.id;
    }
  }

  compareDelinquent(value: any, selected: any): boolean {
    if (value) {
      return value === (selected ? 'Yes' : 'No');
    }
  }

  approve() {
    this.company.status = 'APPROVED';
    this.save(ENDPOINTS.approveCompany);
  }

  reject() {
    this.company.status = 'DISAPPROVED';
    this.update('');
  }

  closeModal() {
    this.dialogRef.close();
  }

  setDelinquent(delinquentVal: any): boolean {
    return delinquentVal === 'Yes' ? true : false;
  }

}
